/* ==========================================================================
    HISTORY
   ========================================================================== */
.history {
	position: relative;
	z-index: 2;
	overflow: hidden;
}

.history__item {
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
	padding: 20px;

	@include media-tablet {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: 30px 20px;
	}
}

.history__fig {
	width: 100%;
	max-width: 380px;

	@include media-phone {
		margin: 0 auto;
	}

	@include media-tablet {
		width: 40%;
	}
}

.history__img {
	position: relative;
	padding-bottom: 78.9473%;

	img {
		position: absolute;
	}
}

.history__txt {
	line-height: 1.6;

	@include media-tablet {
		width: 50%;
		max-width: 490px;
	}
}

.history__xl {
	@include subFont;
	padding-bottom: 0;
	line-height: 1.2;
	font-size: 36px;
	font-weight: 700;
	letter-spacing: 3px;

	@include media-tablet {
		font-size: 48px;
	}
}

.history__nav {
	box-sizing: border-box;
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
	padding: 45px 20px 70px 20px;

	@include media-tablet {
		padding-bottom: 135px;
	}
}

.history__axis {
	position: relative;
	width: 100%;
	height: 2px;
	background: $clrBrown;
}

.history__year {
	position: absolute;
	top: -7px;
	text-align: center;
	cursor: pointer;

	&:before {
		transition: all 0.3s ease-out 0s;
		content: '';
		position: absolute;
		z-index: 1;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 16px;
		height: 16px;
		background: $clrBrown;
		border-radius: 50%;
	}

	&:after {
		transition: all 0.3s ease-out 0s;
		content: '';
		position: absolute;
		z-index: 2;
		top: 7px;
		left: 50%;
		transform: translateX(-50%);
		width: 0;
		height: 2px;
		background: #fef2e6;
		opacity: 0;
	}

	p {
		@include subFont;
		transition: all 0.3s ease-out 0s;
		position: absolute;
		top: 13px;
		left: 50%;
		transform: translateX(-50%);
		color: $clrBrown;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 1.8px;
	}

	.icon {
		transition: all 0.3s ease-out 0s;
		position: absolute;
		z-index: 3;
		top: -12px;
		left: 50%;
		transform: translateX(-50%);
		color: $clrBrown;
		font-size: 0;
		opacity: 0;
	}
}

.history__year.is-active {
	p {
		font-size: 32px;
	}

	&:before {
		opacity: 0;
		width: 0;
		height: 0;
	}

	&:after {
		width: 58px;
		opacity: 1;
	}

	.icon {
		opacity: 1;
		font-size: 33px;
	}
}

.history__year--2012 {
	left: 0;
	text-align: left;
}

.history__year--2013 {
	left: 25%;
}

.history__year--2017 {
	left: 50%;
}

.history__year--2019 {
	left: 75%;
}

.history__year--2020 {
	left: 100%;
}