/* ==========================================================================
    SOCIALS
   ========================================================================== */
.socials ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.socials li {
    padding: 0 17px;
    line-height: 1;
}

.socials a {
    color: $clrNav;
    font-size: 24px;

    &:hover,
    &:focus,
    &:active {
        color: $clrPrimary;
    }
}