/* ==========================================================================
    FIND US
   ========================================================================== */
.find-us {
    position: relative;

    @include media-desktop {
        height: 840px;
    }
}

.find-us__prologue {
    @include media-tablet {
        width: 458px;
    }

    h2 {
        padding-bottom: 10px;
        text-transform: uppercase;
    }
}

.find-us__btns {
    padding: 30px 0;

    @include media-tablet {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .btn {
        display: block;
        margin: 10px 0;

        @include media-tablet {
            width: calc(50% - 10px);
        }
    }
}

.find-us__fig {
    position: relative;
    padding: 0 0 25% 40px;

    @include media-tablet {
        width: 580px;
        margin: 0 auto;
        padding: 0 0 140px 0;  
    }

    @include media-desktop {
        position: absolute;
        top: 180px;
        right: -120px;
    }
}

.find-us__img {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    position: relative;
    z-index: 1;
    padding-bottom: 68.9655%;
    transform: scale(0.95);
    opacity: 0;

    img {
        position: absolute;
    }
}

.find-us__img.is-animated {
    transform: none;
    opacity: 1;
}

.find-us__img--secondary {
    position: absolute;
    z-index: 0;
    left: -10px;
    bottom: 0;
    width: 50%;
    padding: 0;

    @include media-tablet {
        left: -258px;
        bottom: 0;
        width: 420px;
    }

    img {
        position: relative;
    }
}

