/* ==========================================================================
    SUBNAV
   ========================================================================== */
.subnav {
    overflow-x: auto;
    overflow-y: hidden; 
    box-sizing: border-box;
    width: calc(100% + 40px);
    margin: 0 0 0 -20px;
    padding: 0 20px;
}

.subnav ul {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

}

.subnav__btn {
    @include trans2;
    @include subFont;
    position: relative;
    display: block;
    padding: 17px 20px 17px 60px;
    border: 2px solid transparent;
    color: $clrTxt;
    font-size: 14px;
    letter-spacing: 1.6px;
    font-weight: 500;
    white-space: nowrap;
    text-transform: uppercase;

    .icon {
        position: absolute;
        top: 50%;
        left: 18px;
        transform: translateY(-50%);
        font-size: 34px;
    }

    .icon--heart {
        font-size: 24px;
    }
}

.is-active .subnav__btn,
.subnav__btn:hover,
.subnav__btn:focus,
.subnav__btn:active {
    border-color: $clrPrimary;
}