/* ==========================================================================
    BREAD
   ========================================================================== */
.bread {
    padding: 10px 0 0 0;

    @include media-tablet {
        padding-top: 20px;
    }    
}

.bread ul {
    overflow: hidden;
    width: calc(100% + 20px);
    white-space: nowrap;
}

.bread li {
    @include subFont;
    display: inline-block;
    font-size: 14px;
    padding: 0 0 0 10px;
    text-transform: uppercase;
    letter-spacing: 1.6px;

    &:after {
        content: '/';
        position: absolute;
        top: 0;
        left: 0;
        color: $clrBrown;
        font-size: 14px;
    }

    &:first-child {
        padding-left: 0;

        &:after {
            display: none;
        }
    }

    &:last-child {
        font-weight: 700;
        font-size: 16px;
        letter-spacing: 0.6px;
        text-transform: none;

        a {
            color: $clrTxt;
            text-decoration: none;
        }
    }
}

.bread a {
    display: block;
    color: $clrBrown;
    text-decoration: underline;

    &:hover,
    &:focus,
    &:active {
        color: $clrPrimary;
    }
}