/* ==========================================================================
    ARTICLE
   ========================================================================== */
.article {
	max-width: 820px;
}

.article__cols {
	display: flex;
	flex-direction: column;

	@include media-tablet {
		flex-direction: row;
		padding-left: 60px;
	}
}

.article__col {
	@include media-tablet {
		padding-left: 60px;
	}
}

.article__num {
	position: absolute;
	top: 2px;
	left: 0;
	font-weight: 700;
}

.article h1 {
	padding-bottom: 30px;
	font-size: 32px;
}

.article h2 {
	padding: 17px 0 5px 0;
	font-size: 24px;

	&:after {
		display: none;
	}
}

.article h3 {
	padding-bottom: 0;
	color: $disabledClr;
	font-size: 20px;
}

.article ul {
	padding-left: 15px;

	@include media-tablet {
		padding-left: 60px;
	}
}

.article li {
	padding: 2px 0 2px 50px;
}

.article a {
	color: $clrTxt;
	font-weight: 700;
	text-decoration: underline;

	&:hover,
	&:focus,
	&:active {
		text-decoration: none;
	}
}

.article a[href^="tel"],
.article a[href^="sms"] {
	text-decoration: none;
}

