/* ==========================================================================
    NAV
   ========================================================================== */
.nav {
	width: 100%;

	@include media-desktop {
		width: auto;
	}
}

.nav ul {
	display: flex;
	flex-direction: column;

	@include media-desktop {
		flex-direction: row;
	}
}

.nav li {
	@include subFont;
	padding: 15px 17px;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: 2px;
	text-transform: uppercase;
}

.nav a {
	display: block;
	color: $clrNav;
	text-align: center;

	&:hover,
	&:focus,
	&:active {
		color: $clrPrimary;
	}
}

.nav .is-active>a {
	color: $clrPrimary;
}