/* ==========================================================================
    ICO
   ========================================================================== */
.ico {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 12px;

	&:before {
		content: '';
		display: block;
		padding-top: 100%;
	}
} 

.ico__svg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	vertical-align: top;
	fill: #000;
	transform-origin: 0 0 0;
	transition: all 0.3s ease-in-out 0s;

	&:hover {
		fill: #000;
	}
}