/* ==========================================================================
    HAMBURGER
   ========================================================================== */
@mixin transHamburgerLines {
	transition: all 0.3s cubic-bezier(0.33333, 0.66667, 0.66667, 1);
}

$hamBg: $clrTxt; 

.hamburger {
	@include transHamburgerLines;
	@include subFont;
	overflow: hidden;
	position: relative;
	z-index: 10;
	width: 46px;
	height: 46px;
	margin: 0 -5px 0 0; 
	text-align: left;

	@include media-tablet {
		transform: scale(1.5);
	}

	@include media-desktop {
		display: none;
	}
}

.hamburger a {
	box-sizing: border-box;
	position: relative;
	display: block;
	width: 100%;
	height: 46px;
	padding: 0;
	color: $clrTxt;
	text-decoration: none;
	line-height: 46px;
	text-transform: uppercase;
	outline: none;
}

.hamburger a:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	line-height: 100%;
	cursor: pointer;
}

.hamburger .lines {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
}

.hamburger .lines span  {
	@include transHamburgerLines;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 32px;
	height: 2px;
	margin: -1px 0 0 0;
	transform: translateX(-50%);
	background: $hamBg;
}

.hamburger .lines:before,
.hamburger .lines:after {
	@include transHamburgerLines;
	content: '';
	position: absolute;
	width: 32px;
	height: 2px;
	background: $hamBg;
}

.hamburger .lines:before {
	transform-origin: 0 0;
	top: 10px;
	left: 6px;
}

.hamburger .lines:after {
	transform-origin: 0 100%;
	bottom: 11px;
	left: 6px;
}

.hamburger .txt {
	@include transHamburgerLines;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 10px;
	letter-spacing: 1px;
}

.nav-open {
	.hamburger .lines span {
		width: 0;
		opacity: 0;
	}

	.hamburger .lines:before {
		transform: rotate(45deg);
		left: 14px;
	}

	.hamburger .lines:after {
		transform: rotate(-45deg);
		left: 14px;
	}

	.hamburger .txt {
		opacity: 0;
		margin-left: 40px;
		font-size: 0;
	}
}
