/* ==========================================================================
    CONTACT
   ========================================================================== */
.contact {
	display: flex;
	flex-direction: column;
	background: $clrLight;
	box-shadow: 0 0 35px 13px rgba(119, 89, 96, 0.2);

	@include media-tablet {
		flex-direction: row;
	}
}

.contact__main {
	box-sizing: border-box;
	padding: 20px;

	@include media-tablet {
		width: 50%;
		padding: 80px 60px;
	}

	@include media-desktop {
		width: 43.8%;
	}
}

.contact__aside {
	background-size: cover;
	background-position: 50% 50%;

	@include media-phone {
		height: 200px;
	}

	@include media-tablet {
		width: 50%;
	}

	@include media-desktop {
		width: 56.2%;
	}
}

