/* ==========================================================================
    FORM
   ========================================================================== */
.form {

}

.form__row {
	position: relative;
	padding: 0 0 10px 0;
}

.form__label {
	@include subFont;
	display: block;
	padding: 0 0 6px 0;
	color: $clrBrown;
	font-size: 12px;
	line-height: 1.2;
	font-weight: 500;
}

.form__input {
	@include trans2;
	@include subFont;
	box-sizing: border-box;
	width: 100%;
	height: 44px;
	padding: 10px 15px;
	background: $inputBg;
	border: 1px solid $inputBg;
	outline: none;
	color: $inputClr;
	font-size: 16px;
	font-weight: 500;

	&:focus {
		border-color: $clrBrown;
	}
}

.form__textarea {
	@include trans2;
	@include subFont;
	box-sizing: border-box;
	overflow: auto;
	width: 100%;
	height: 158px;
	padding: 15px;
	background: $inputBg;
	border: 0;
	outline: none;
	resize: vertical;
	color: $inputClr;
	font-size: 16px;
	font-weight: 500;

	&:focus {
		border-color: $clrBrown;
	}
}

/* Form messages
   ========================================================================== */
.form__input.is-filled,
.form__textarea.is-filled {
	background-image: url(../images/success.svg);
	background-position: right 15px center;
	background-repeat: no-repeat;
	border-color: $clrBrown;
}

.form__input.is-error,
.form__textarea.is-error {
	background-image: url(../images/error.svg);
	background-position: right 15px center;
	background-repeat: no-repeat;
	border-color: $clrError;
	color: $clrError;
}

.form__error {
	@include subFont;
	display: block;
	color: $clrError;
	font-size: 12px;
	line-height: 1.2;
	font-weight: 500;

	@include media-phone {
		padding: 5px 0 0 0;
	}

	@include media-tablet {
		position: absolute;
		top: 0;
		right: 0;
	}

	&:empty {
		display: none;
	}
}

.checkbox+.form__error {
	padding: 0 0 5px 40px;
	
	@include media-tablet {
		position: relative;
	}
}

.form__btns .form__error {
	position: relative;
	padding: 10px 0 0 0;
	text-align: center;
}