/* ==========================================================================
    Magnific Popup 1.0.0 - custom
   ========================================================================== */
// Overlay Opacity
.mfpOverlayOpacity {
    opacity: 0.8;
    filter: alpha(opacity=80);
}

// Overlay Color
$mfpOverlayColor: #000;

// Text color
$mfpTextColor: #fff;

// Gallery arrow bg & opacity
.galleryArrow {
    background: #000;
    opacity: .5;
    filter: alpha(opacity=50);
}

// Sprite
.mfpSprite {
    background-image: url(../images/magnific-popup.png);
    background-size: 160px 40px;
    background-color: transparent;
}

.mfpSprite-old {
    background-image: url(../images/magnific-popup-ie.png);
    background-size: inherit;
    background-color: transparent;
}

.mfp-bg {
    @extend .mfpOverlayOpacity;
    overflow: hidden;
    position: fixed;
    z-index: 100;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: $mfpOverlayColor;
}

.mfp-wrap {
    position:fixed;
    z-index: 101;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline:none!important;
    -webkit-backface-visibility:hidden
}

.mfp-container {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    padding: 0 8px;
    text-align: center;
}

.mfp-container:before {
    content:'';
    display: inline-block;
    height: 100%;
    vertical-align: middle
}

.mfp-align-top .mfp-container:before {
    display: none
}

.mfp-content {
    position: relative;
    z-index: 105;
    display: inline-block;
    margin: 0 auto;
    vertical-align: middle;
    text-align: left;    
}

.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
    width: 100%;
    cursor: auto;
}

.mfp-close-btn,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
    -webkit-user-select:none;
    -moz-user-select:none;
    user-select:none
}

.mfp-loading.mfp-figure {
    display:none
}

.mfp-hide {
    display:none!important
}

.mfp-preloader {
    color:#CCC;
    position:absolute;
    top:50%;
    width:auto;
    text-align:center;
    margin-top:-.8em;
    left:8px;
    right:8px;
    z-index:1044
}

.mfp-preloader a {
    color:#CCC
}

.mfp-preloader a:hover {
    color:#FFF
}

.mfp-s-ready .mfp-preloader {
    display:none
}

.mfp-s-error .mfp-content {
    display:none
}

button.mfp-close-btn,
button.mfp-arrow {
    overflow: hidden;
    z-index: 106;
    display: block;
    padding: 0;
    border:0;
    background-color: transparent;
    -webkit-appearance:none;
    outline:none;
    box-shadow:none;
    cursor:pointer;
    text-align: left;
    text-indent: -100px;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0
}

button.mfp-close-btn {
    @extend .mfpSprite;
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
    height: 40px;
    margin: 0;
    padding: 0;
    background-position: -40px 0;
}

.no-backgroundsize button.mfp-close-btn {
    @extend .mfpSprite-old;
}

.mfp-close-btn-in .mfp-close-btn {
    background-position: 0 0;
}

.mfp-image-holder .mfp-close-btn,
.mfp-iframe-holder .mfp-close-btn {
    width: 40px;
}

.mfp-close-btn-in .mfp-image-holder .mfp-close-btn,
.mfp-close-btn-in .mfp-iframe-holder .mfp-close-btn {
    margin-right: -12px;
    background-position: -40px 0;
}

.mfp-counter {
    position: absolute;
    top: 0;
    right: 0;
    color: $mfpTextColor;
    font-size: 13px;
    line-height: 18px;
    white-space: nowrap;
}

.mfp-arrow {
    position: absolute;
    top: 50%;
    width: 90px;
    height: 200px;
    margin: 0;
    margin-top: -100px;
    padding: 0;
}

.mfp-arrow-left {
    left: 0;
}

.mfp-arrow-left:before {
    @extend .mfpSprite;
    content: '';
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 10px;
    display: block;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 0;
    background-position: -80px 0; 
}

.no-backgroundsize .mfp-arrow-left:before {
    @extend .mfpSprite-old;
}

.mfp-arrow-left:after {
    @extend .galleryArrow;
    content: '';
    position: absolute;
    z-index: 1;
    text-indent: 50%;
    left: 10px;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 0;
}

.mfp-arrow-right {
    right: 0;
}

.mfp-arrow-right:before {
    @extend .mfpSprite;
    content: '';
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 10px;
    display: block;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 0;
    background-position: -120px 0;
}

.no-backgroundsize .mfp-arrow-right:before {
    @extend .mfpSprite-old;
}

.mfp-arrow-right:after {
    @extend .galleryArrow;
    content: '';
    position: absolute;
    z-index: 1;
    text-indent: 50%;
    right: 10px;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 0;
}

.mfp-iframe-holder {
    padding-top:40px;
    padding-bottom:40px
}

.mfp-iframe-holder .mfp-content {
    line-height:0;
    width:100%;
    max-width:900px
}

.mfp-iframe-holder .mfp-close-btn {
    top: -40px;
}

.mfp-iframe-scaler {
    overflow: hidden;
    width: 100%;
    height: 0;
    padding-top: 56.25%;
}

.mfp-iframe-scaler iframe {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    box-shadow: 0 0 8px rgba(0,0,0,0.6);
}

// Main image in popup
img.mfp-img {
    box-sizing: border-box;
    width: auto;
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
    padding: 40px 0;
    line-height: 0;
}

// The shadow behind the image
.mfp-figure {
    line-height: 0;
}

.mfp-figure:after {
    content: '';
    position: absolute;
    z-index: -1;
    right: 0;
    left: 0;
    top: 40px;
    bottom: 40px;
    display: block;
    width: auto;
    height: auto;
    background: #444;
    box-shadow: 0 0 8px rgba(0,0,0,0.6);
}

.mfp-figure small {
    display: block;
    color: #bdbdbd;
    font-size: 12px;
    line-height: 14px;
}

.mfp-figure figure {
    margin: 0;
}

.mfp-bottom-bar {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top:-36px;
    cursor: auto;
}

.mfp-title {
    padding-right: 36px;
    color: $mfpTextColor;
    font-size: 15px;
    line-height: 18px;
    text-align: left;
    word-wrap: break-word;
}

.mfp-image-holder .mfp-content {
    max-width: 100%
}

.mfp-gallery .mfp-image-holder .mfp-figure {
    cursor: pointer
}

@media screen and (max-width: 800px) and (orientation: landscape),screen and (max-height: 300px) {
    .mfp-img-mobile .mfp-image-holder {
        padding-left: 0;
        padding-right: 0;
    }

    .mfp-img-mobile img.mfp-img {
        padding: 0;
    }

    .mfp-img-mobile .mfp-figure:after {
        top: 0;
        bottom: 0;
    }

    .mfp-img-mobile .mfp-figure small {
        display: inline;
        margin-left: 5px;
    }

    .mfp-img-mobile .mfp-bottom-bar {
        box-sizing: border-box;
        position: fixed;
        top: auto;
        bottom: 0;
        margin: 0;
        padding: 3px 5px;
        background: rgba(0,0,0,0.6);
    }

    .mfp-img-mobile .mfp-bottom-bar:empty {
        padding: 0;
    }

    .mfp-img-mobile .mfp-counter {
        right: 5px;
        top: 3px
    }

    .mfp-img-mobile .mfp-close-btn {
        position: fixed;
        top: 0;
        right: 0;
        width: 35px;
        height: 35px;
        padding: 0;
        background: rgba(0,0,0,0.6);
        line-height: 35px;
        text-align: center;
    }
}

@media all and (max-width: 900px) {
    .mfp-container {
        padding-left: 6px;
        padding-right: 6px
    }   
}

/* ANIMATION
    ========================================================================== */
.mfp-fade.mfp-bg {
    opacity: 0;
    filter: alpha(opacity=0);
    transition: all 0.25s ease-out;
}

// overlay animate in
.mfp-fade.mfp-bg.mfp-ready {
    @extend .mfpOverlayOpacity;
}

// overlay animate out
.mfp-fade.mfp-bg.mfp-removing {
    opacity: 0;
    filter: alpha(opacity=0);
}

// content at start
.mfp-fade.mfp-wrap .mfp-content {
    opacity: 0;
    filter: alpha(opacity=0);
    transition: all 0.25s ease-out;
}

// content animate it 
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
    opacity: 1;
    filter: alpha(opacity=100);
}

// content animate out
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
    opacity: 0;
    filter: alpha(opacity=0);
}