/* ==========================================================================
    CONTENT
   ========================================================================== */
.content {
	padding: 30px 0;

	@include media-tablet {
		padding: 60px 0;
	}	

	.section {
		position: relative;
		z-index: 2;
	}
}

.content--dark {
	background: linear-gradient(181deg, #fff1e7 0%, #fff1e7 64%, #fff1e7 100%);
}

.content--dark-secondary {
	position: relative;
	background: linear-gradient(181deg, #fff1e7 0%, #fff1e7 64%, #fff1e7 100%);

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 425px;
		background: $clrLight;

		@include media-tablet {
			height: 515px;
		}
	}
}

.content--bg1 {
	position: relative;
	padding: 70px 0;

	@include media-tablet {
		padding: 140px 0;
	}

	.content__bg {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 100%;
		background-repeat: no-repeat;
	}

	.content__deco {
		position: absolute;
		top: -80px;
		right: 0;
		width: 190px;
		height: 294px;
		background-size: cover;

		@include media-tablet {
			top: -170px;
			width: 380px;
			height: 589px;
		}

	}
}

.content--bg2 {
	position: relative;

	.content__bg {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 100%;
		background-repeat: no-repeat;
	}
}

.content--bg3 {
	position: relative;

	&:after {
		content: '';
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 46.3%;
		background: $bgTable;
	}
}

.content--bg4 {
	position: relative;
	padding: 60px 0;

	@include media-tablet {
		padding: 154px 0;
	}
	
	.content__bg {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: no-repeat;
	}
}

.content--bg5 {
	position: relative;
	
	@include media-tablet {
		padding: 120px 0 60px 0; 
	}

	.content__bg {
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: no-repeat;
	}

	&:after {
		content: '';
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 11%;
		background: $bgTable; 
	}
}

.content--bg6 {
	position: relative;
	
	.content__bg {
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: repeat-y;
	}
}

.content--bg7 {
	position: relative;
	
	.content__bg {
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: repeat-y;
	}
}

.content--bg8 {
	position: relative;

	.content__bg {
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 70%;
		max-height: 784px;
		background-color: $bgTable;
		background-position: 50% 100%;
		background-repeat: no-repeat;
	}
}

.content--bg9 {
	position: relative;

	.content__bg {
		position: absolute;
		z-index: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: no-repeat;
	}
}

.header+.content {
	padding-top: 12px;

	@include media-tablet {
		padding-top: 24px;
	}
}