/* ==========================================================================
    PLIST
   ========================================================================== */
.plist__list {
    display: flex;
    flex-direction: column;
    padding: 20px 0 40px 0;

    @include media-phone-xl {
        width: calc(100% + 24px);
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: -12px;
    }
}

.plist--secondary {
    padding-bottom: 35px;

    @include media-tablet {
        padding-bottom: 70px;
    }

    .plist__list {
        padding-bottom: 10px;
    }
}

.plist__item {
    box-sizing: border-box;
    width: 100%;
    padding: 12px 0; 

    @include media-phone-xl {
        padding: 12px;
        width: 50%;
    }

    @include media-tablet {
        width: 33.3333%;
    }
}

.plist__link {
    overflow: hidden;
    position: relative;
    display: block;
    padding: 20px 20px 15px 20px;
    background: $clrLight;
    box-shadow: 0 0 16px rgba(58, 31, 30, 0.12);
    color: $clrTxt;

    @include media-tablet {
        padding: 30px 30px 25px 30px;
    }

    &:before {
        transition: all 0.5s ease-out 0s;
        content: '';
        box-sizing: border-box;
        position: absolute;
        top: 5px;
        left: 5px;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        border: 2px solid $clrPrimary;
        opacity: 0;
        transform: scale(1.3);

        @include media-tablet {
            top: 10px;
            left: 10px;
            width: calc(100% - 20px);
            height: calc(100% - 20px);
        }
    }

    &:hover,
    &:focus,
    &:active {
        &:before {
            opacity: 1;
            transform: scale(1);
        }
    }
}

.plist__fig {
    display: block;
    padding-bottom: 12px;
}

.plist__img {
    position: relative;
    display: block;
    padding-bottom: 81.9672%;

    img {
        position: absolute;
    }
}

.plist__content {
    overflow: hidden;
    display: block;

    @include media-phone-xl {
        min-height: 145px;
    }
}

.plist__sub {
    display: block;
    padding: 0 0 11px 0;
    color: $clrLinksActive;
    font-size: 16px;
    line-height: 1.3;
}

.plist__name {
    @include subFont;
    display: block;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;

    @include media-tablet {
        font-size: 24px;
    }
}

.plist__price {
    color: $clrLinksActive;
    font-size: 26px;

    @include media-tablet {
        font-size: 30px;
    }

    .icon {
        position: relative;
        top: 4px;
        padding: 0 4px;
        font-size: 30px;

        @include media-tablet {
            font-size: 34px;
        }
    }
}

.plist__more {
    padding: 10px 0;
    text-align: center;
}