/* ==========================================================================
    POST
   ========================================================================== */
.post {
    h1 {
        @include media-tablet {
            padding-bottom: 38px;
        }
    }
}

.post__meta {
    @include subFont;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 0 0 10px 0;
    color: $clrLinksActive;
    letter-spacing: 1.6px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;

    .icon {
        padding-right: 20px;
        font-size: 34px;
    }
}

.post__intro {
    position: relative;
    padding: 0 0 20px 0;

    @include media-tablet {
        padding: 0 52% 40px 0;
    }

    .post__fig {
        padding: 0 0 20px 0;

        @include media-tablet {
            position: absolute;
            top: 30px;
            right: 0;
        }
    }
}

.post__row {
    display: flex;
    flex-direction: column;
    padding: 30px 0;

    @include media-tablet {
        flex-direction: row;
        justify-content: space-between;
        padding: 70px 0;
    }
}

.post__row--primary {

    .post__fig {
        @include media-tablet {
            order: -1;
        }
    }

    .post__article {
        @include media-tablet {
            padding-left: 0;
        }
    }
}

.post__article {
    box-sizing: border-box;
    padding-bottom: 30px;
    font-size: 20px;

    @include media-tablet {
        width: 45%;
        max-width: 400px;
        padding-left: 9%;
        padding-bottom: 0;
        font-size: 24px;
    }

    li {
        &:before {
            top: 18px;

            @include media-tablet {
                top: 21px;
            }
        }
    }
}

.post__fig {
    @include media-tablet {
        width: 50%;
        max-width: 400px;
    }
}

