// ==========================================================================
//   MEDIA QUERY
// ==========================================================================
$phone-xl-width: 33.75em;   // 540px
$tablet-width: 48em;        // 768px
$desktop-width: 64em;       // 1024px
$pixel: 0.0625em;           // 1px

@mixin media-phone {
    @media (max-width: #{$tablet-width - $pixel}) {
        @content;
    }
}

@mixin media-phone-xl {
    @media (min-width: #{$phone-xl-width}) {
        @content;
    }
}

@mixin media-tablet-range {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - $pixel}) {
        @content;
    }
}

@mixin media-tablet {
    @media (min-width: #{$tablet-width}) {
        @content;
    }
}

@mixin media-desktop {
    @media (min-width: #{$desktop-width}) {
        @content;
    }
}

