/* ==========================================================================
    FAKE INPUT
   ========================================================================== */
// Mixins
@mixin transInputMark {
    transition: all 0.25s ease-in-out 0s;
}

// Variables
$fake-bg: #fff; 
$fake-bg-checked: #fff;
$fake-transition: opacity 0.33s ease-in-out 0s;
$fake-border: $clrBrown;
$fake-check: $clrBrown;
$fake-radio: $clrPrimary;

.checkbox,
.radio {
	@include subFont;
    position: relative;
    display: block;
    margin: 10px 0 10px 9px; 
    padding: 4px 0 0 31px;
    line-height: 1.4;
    font-size: 16px;
    font-weight: 500;
}

.checkbox input,
.radio input {
    position: absolute;
    top: 4px;
    left: 0;
    opacity: 0;
}

.checkbox__mark {
    box-sizing: border-box;
    position: absolute;
    top: 4px;
    left: 0;
    width: 20px;
    height: 20px;
    background: $fake-bg;
    border: 1px solid $fake-border;
    border-radius: 2px;
    cursor: pointer;

    &:before {
        @include iconFont;
        @extend .icon--checkbox:before;
        transition: $fake-transition;
        will-change: opacity;
        position: absolute;
        top: 0;
        right: -6px;
        color: $fake-check;
        font-size: 14px;
        opacity: 0;
    }
}

.checkbox input[type=checkbox]:checked+.checkbox__mark {
    background: $fake-bg-checked;

    &:before {
        opacity: 1;
    }
}

.radio__mark {
    box-sizing: border-box;
    position: absolute;
    top: 4px;
    left: 0;
    width: 20px;
    height: 20px;
    background: $fake-bg;
    border: 1px solid $fake-border;
    border-radius: 50%;
    cursor: pointer;

    &:before {
        will-change: opacity;
        transition: $fake-transition;
        backface-visibility: hidden;
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 8px;
        height: 8px;
        margin: -4px 0 0 -4px;
        background: $fake-radio;
        border-radius: 50%;
        opacity: 0;
    }
}

.radio input[type=radio]:checked+.radio__mark {
    background: $fake-bg-checked;

    &:before {
        opacity: 1;
    }
}