/* ==========================================================================
    STORE
   ========================================================================== */
.store {
    display: flex;
    flex-direction: column;
    padding: 0 0 30px 0;

    @include media-tablet {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 80px;
    }

    h1,
    h2 {
        padding: 0 0 19px 0;
        letter-spacing: 0;
    }

    h2 {
        font-size: 36px;

        @include media-tablet {
            font-size: 48px;
        }

        &:after {
            @include media-tablet {
                margin-top: 29px;
            }
        }
    }
}

.store--secondary {
    padding-top: 30px;

    @include media-tablet {
        padding-top: 70px;
    }
}

.store__main {
    padding: 0 0 20px 0;

    @include media-tablet {
        width: 60%;
        max-width: 520px;
        padding-bottom: 40px;
    }
}

.store__aside {
    padding: 0 0 20px 0;

    @include media-tablet {
        width: 35%;
        max-width: 328px;
        padding-top: 40px;
        padding-bottom: 40px;
    }
}

.store__foot {
    display: flex;
    flex-direction: column;

    @include media-tablet {
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 100%;
    }

    .btn {
        width: 100%;
        margin: 10px 0;

        @include media-tablet {
            width: calc(50% - 10px);
        }

        @include media-desktop {
            width: calc(25% - 20px);
        }
    }
}

.store__address {
    @include subFont;
    padding-bottom: 8px;
    color: $clrBrown;
    letter-spacing: 1.8px;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
}

.store__xl {
    @include subFont;
    padding-bottom: 3px;
    font-size: 24px;
    font-weight: 700;
}

.store__opening {
    width: 100%;

    td {
        text-align: center;

        &:last-child {
            @include subFont;
            font-weight: 700;
            font-size: 18px;
        }
    }
}