/* ==========================================================================
    FOOTER
   ========================================================================== */
.footer {
	flex: none;
	padding: 40px 0;
	background: $bgTable url(../images/footer.png) no-repeat 50% 0;

	@include media-tablet {
		padding: 80px 0 120px 0;
	}
}

.footer--secondary {
	background: $clrLight url(../images/footer-white.png) no-repeat 50% 0;	
}

.footer__main {
	position: relative;
	display: flex;
	flex-direction: column;

	@include media-tablet {
		flex-direction: row;
		justify-content: space-between;
	}
}

.footer__logo {
	padding: 0 0 20px 0;

	@include media-tablet {
		padding-top: 60px;
	}
}

.footer__items {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;

	@include media-tablet {
		padding: 40px 0 0 0;
	}
}

.footer__item {
	padding: 20px 0;
	font-size: 16px;

	@include media-tablet {
		padding-left: 96px;
	}

	&:first-child {
		@include media-phone {
			padding-right: 30px;
		}
	}


	.xl {
		@include subFont;
		padding: 0;
		font-size: 20px;
		font-weight: 700;
		line-height: 1.2;

		@include media-tablet {
			font-size: 24px;
		}
	}

	.xl+p {
		padding: 5px 0 20px 0;
	}
}

.footer__socials {
	padding: 20px 0 0 0;

	@include media-tablet {
		position: absolute;
		top: -14px;
		right: 0;
	}

	.socials li {
		padding: 0 48px 0 0;

		@include media-tablet {
			padding: 0 0 0 48px;
		}
	}
}
