/* ==========================================================================
    ISTREAM
   ========================================================================== */
.istream {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;
}

.istream__item {
    width: 250px;
    padding: 12px;
}

.istream__link {
    display: block;
}

.istream__img {
    position: relative;
    display: block;
    padding-bottom: 100%;

    img {
        position: absolute;
    }
}