/* ==========================================================================
    WHO
   ========================================================================== */
.who {
    position: relative;

    @include media-tablet {
        padding: 104px 0;
        height: 625px;
    }
}

.who__prologue {
    padding: 0 0 40px 0;

    @include media-tablet {
        max-width: 345px;
    }

    h2 {
        padding-bottom: 10px;
        text-transform: uppercase;
    }

    .btn {
        margin-top: 24px;

        @include media-tablet {
            margin-top: 48px;
        }
    }
}

.who__item {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    position: relative;
    color: $clrBrown;
    padding: 0 0 30px 0;
    transform: translateY(40px);
    opacity: 0;

    @include media-tablet {
        position: absolute;
    }

    h3 {
        padding-bottom: 0;
        font-size: 24px;
    }

    p {
        padding-top: 3px;
    }
}

.who__item.is-animated {
    transform: none;
    opacity: 1;
}

.who__fig {
    width: 100%;
    max-width: 308px;

    @include media-tablet {
        width: 308px;
    }
}

.who__img {
    position: relative;
    padding-bottom: 100%;

    img {
        position: absolute;
    }
}

.who__arrow {
    position: absolute;

    @include media-phone {
        display: none;
    }
}

.who__item--1 {
    @include media-tablet {
        top: 40px;
        right: 47px;
    }

    .who__fig {
        @include media-tablet {
            padding-top: 20px; 
        }
    }

    .who__img {
        padding-bottom: 103.5714%;
    }

    .who__arrow {
        @include media-tablet {
            top: 55px;
            right: -50px;
        }
    }
}

.who__item--2 {
    @include media-tablet {
        bottom: 35px;
        right: 10%;
    }

    .who__fig {
        @include media-tablet {
            position: absolute;
            top: -175px;
            left: -340px;
        }
    }

    .who__arrow {
        @include media-tablet {
            bottom: -62px;
            left: -168px;
        }
    }
}