@font-face {
    font-family: "icons";
    src: url('../fonts/icons.eot');
    src: url('../fonts/icons.eot?#iefix') format('eot'),
        url('../fonts/icons.woff2') format('woff2'),
        url('../fonts/icons.woff') format('woff'),
        url('../fonts/icons.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

[class^="icon--"],
[class*=" icon--"] {
    font-family: "icons"!important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon--allergen:before { content: "\EA01" }
.icon--cake:before { content: "\EA02" }
.icon--candy:before { content: "\EA03" }
.icon--checkbox:before { content: "\EA04" }
.icon--diameter:before { content: "\EA05" }
.icon--facebook:before { content: "\EA06" }
.icon--heart:before { content: "\EA07" }
.icon--info:before { content: "\EA08" }
.icon--instagram:before { content: "\EA09" }
