/* ==========================================================================
    SLIDER
   ========================================================================== */
.slider {
    padding: 15px 0;

    @include media-tablet {
        padding: 30px 0;
    }
}

.slider__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.slider__fig {
    width: 61.07%;
}

.slider__fig--secondary {
    width: 37.36%;

    .slider__img {
        padding-bottom: 125.2788%;
    }
}    

.slider__img {
    position: relative;
    padding-bottom: 76.59%;

    img {
        position: absolute;
    }
}   

.slider__arrows {
    position: relative;
}