/* ==========================================================================
    NOT FOUND
   ========================================================================== */
.not-found {
    position: relative;
    
    @include media-tablet {
        max-height: 784px; 
    }
}

.not-found__logo {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: scale(0.85);
    padding: 0 0 20px 0;
    opacity: 0;

    @include media-tablet {
        padding-bottom: 10vh;
    }
}

.not-found__logo.is-active {
    transform: none;
    opacity: 1;
}

.not-found__main {
    @include clearfix;
    box-sizing: border-box;
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 20px;
}

.not-found__bg {
    position: absolute;
    z-index: 0;
    bottom: 13%;
    right: 0;
    width: 150px;
    height: 131px;
    background-size: 100% auto;
    background-repeat: no-repeat;

    @include media-tablet {
        width: 351px;
        height: 357px;
    }
}

.not-found__deco {
    transition: transform 1.5s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    position: absolute;
    z-index: 1;
    top: -50px;
    right: 50%;
    transform: scale(0.95);
    width: 600px;
    height: 418px;
    margin-right: -50%;
    background-size: 100% auto;
    background-repeat: no-repeat;
    opacity: 0;


    @include media-tablet {
        top: -256px;
        width: 1563px;
        height: 1090px;
        margin-right: -340px;
    }
}

.not-found__deco.is-active {
    transform: none;
    opacity: 1;

    @include media-phone {
        opacity: 0.4;
    }
}

.not-found__article {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: translateY(50px);
    padding: 0 0 30px 0;
    font-size: 20px;
    opacity: 0;

    @include media-tablet {
        float: right;
        width: 70%;
        max-width: 370px;
        padding-bottom: 20vh;
        font-size: 24px;
    }
}

.not-found__article.is-active {
    transform: none;
    opacity: 1;
}

.not-found__article h1 {
    letter-spacing: 2.23px;
    padding-bottom: 18px;
    text-transform: uppercase;
}

.not-found__article .btn {
    margin-top: 20px;
}

.not-found__meta {
    @include subFont;
    margin-bottom: -13px;
    color: $clrBrown;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1.8px;
    font-weight: 500;
}