/* ==========================================================================
    CITATION
   ========================================================================== */
.citation {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100vh;
    max-height: 400px;
    background-position: 50% 50%;
    text-align: center;

    @include media-tablet {
        max-height: 800px;
    }
}

.citation__article {
    box-sizing: border-box;
    width: 100%;
    max-width: 660px;
    padding: 0 20px;
}

.citation .promo {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: scale(0.85);
    opacity: 0;

    @include media-tablet {
        font-size: 16px;
    }
}

.citation .promo.is-animated {
    transform: none;
    opacity: 1;
}

.citation .xl {
    @include subFont;
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: scale(0.90);
    opacity: 0;
    padding-top: 20px;
    font-size: 26px;
    font-weight: 700;
    line-height: 1.45;
    letter-spacing: 2.2px;

    @include media-tablet {
        padding-top: 36px;
        font-size: 32px;
    }

    &:after {
        content: '';
        display: block;
        width: 86px;
        height: 2px;
        margin: 30px auto 0 auto;
        background: $clrBrown;

        @include media-tablet {
            margin-top: 60px;
        }
    }
}

.citation .xl.is-animated {
    transform: none;
    opacity: 1;
}