/* ==========================================================================
    PERSON
   ========================================================================== */
.person {
	display: flex;
	flex-direction: column;
	padding: 20px 0;

	@include media-tablet {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: 55px 0;
	}
}

.person--secondary {
	.person__fig {
		@include media-tablet {
			order: -1;
		}
	}
}

.person__article {
	padding: 10px 0;

	@include media-tablet {
		width: 45%;
		max-width: 400px;
	}

	h2 {
		padding-bottom: 0;

		&:after {
			display: none;
		}
	}

	h2+.person__promo {
		padding-top: 3px;
	}
}

.person__fig {
	@include media-tablet {
		width: 45%;
		max-width: 400px;
	}
}

.person__img {
	transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
	position: relative;
	transform: scale(0.95);
	opacity: 0;
	padding-bottom: 100%;


	img {
		position: absolute;
	}
}

.person__img.is-animated {
	transform: none;
	opacity: 1;
}

.person__promo {
	color: $clrBrown;
	font-size: 16px;
}