/* ==========================================================================
    HERO
   ========================================================================== */
.hero {
    position: relative;
    
    @include media-tablet {
        max-height: 784px; 
    }
}

.hero__logo {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: scale(0.85);
    padding: 0 0 20px 0;
    opacity: 0;

    @include media-tablet {
        padding-bottom: 10vh;
    }
}

.hero__logo.is-active {
    transform: none;
    opacity: 1;
}

.hero__main {
    @include clearfix;
    box-sizing: border-box;
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 20px;
}

.hero__bg {
    position: absolute;
    z-index: 0;
    bottom: 10%;
    right: 0;
    width: 151px;
    height: 191px;
    background-size: 100% auto;
    background-repeat: no-repeat;

    @include media-tablet {
        width: 302px;
        height: 382px;
    }
}

.hero__cake {
    transition: transform 1.5s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    position: absolute;
    transform: scale(0.95);
    z-index: 1;
    top: -150px;
    right: 50%;
    width: 600px;
    height: 619px;
    margin-right: -30%;
    background-size: 100% auto;
    background-repeat: no-repeat;
    opacity: 0;

    @include media-tablet {
        top: -390px;
        width: 1199px;
        height: 1237px;
        margin-right: -40px;
    }
}

.hero__cake.is-active {
    transform: none;
    opacity: 1;

    @include media-phone {
        opacity: 0.4;
    }
}

.hero__article {
    transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1), opacity 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    transform: translateY(50px);
    padding: 0 0 30px 0;
    font-size: 20px;
    opacity: 0;

    @include media-tablet {
        float: right;
        width: 70%;
        max-width: 460px;
        padding-bottom: 20vh;
        font-size: 24px;
    }
}

.hero__article.is-active {
    transform: none;
    opacity: 1;
}

.hero__article h1 {
    letter-spacing: 2.23px;
    padding-bottom: 18px;
    text-transform: uppercase;
}

.hero__meta {
    @include subFont;
    margin-bottom: -13px;
    color: $clrBrown;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1.8px;
    font-weight: 500;
}