/* ==========================================================================
    SLICK CUSTOM
   ========================================================================== */
.slick-arrow {
    box-sizing: border-box;
    overflow: hidden;
    position: absolute;
    top: 7px;
    width: 29px;
    height: 46px;
    margin: 0;
    padding: 0;
    background: transparent;
    border: 0;
    color: transparent;
    font-size: 0;
    outline: none;

    @include media-tablet {
        top: 13px;
        width: 33px;
        height: 56px;
    }

    &:before {
        @include trans1;
        @include iconFont;
        position: absolute;
        top: 5px;
        left: 5px;
        color: $clrBrown;
        font-size: 36px;

        @include media-tablet {
            font-size: 46px;
        }
    }

    &:hover {
        &:before {
            color: $clrPrimary;
        }
    }
}

.slick-prev {
    right: 78px;

    @include media-tablet {
        right: 98px;
    }

    &:before {
        @extend .icon--slick-prev:before;
    }
}

.slick-next {
    right: 17px;

    &:before {
        @extend .icon--slick-next:before;
    }
}