/* ==========================================================================
    BODY
   ========================================================================== */
html {
	overflow-x: hidden; 
	height: 100%;
}

body {
	@include baseFont;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	padding: 70px 0 0 0;
	color: $clrTxt;
	line-height: 1.6;
	font-weight: 400;
	font-size: 16px;
	-webkit-font-smoothing: subpixel-antialiased;

	@include media-tablet {
		padding-top: 96px;
		font-size: 18px;
	}
}