/* ==========================================================================
    BTN
   ========================================================================== */
.btn {
	@include trans2;
	@include subFont;
	box-sizing: border-box;
	display: inline-block;
	vertical-align: top;
	padding: 13px 18px;
	background: transparent;
	border: 1px solid $clrNav;
	color: $clrNav;
	font-size: 14px;
	line-height: 1.2;
	font-weight: 500;
	text-transform: uppercase;
	letter-spacing: 1.6px;

	&:hover,
	&:focus,
	&:active {
		color: $clrTxt;
		border-color: $clrPrimary;
		background-color: $clrPrimary;
	}
}

.btn--insta {
	position: relative;
	width: 220px;
	text-align: left;

	&:after {
		@include iconFont;
		@extend .icon--instagram:before;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		font-size: 24px;
	}
}

.btn--fb {
	position: relative;
	width: 220px;
	text-align: left;

	&:after {
		@include iconFont;
		@extend .icon--facebook:before;
		position: absolute;
		top: 50%;
		right: 14px;
		transform: translateY(-50%);
		font-size: 24px;
	}
}

.btn--pin {
	position: relative;
	padding-right: 44px;

	&:after {
		@include iconFont;
		@extend .icon--pin:before;
		position: absolute;
		top: 50%;
		right: 14px;
		transform: translateY(-50%);
		font-size: 24px;
	}
}

.btn--mail {
	position: relative;
	padding-right: 58px;

	&:after {
		@include iconFont;
		@extend .icon--mail:before;
		position: absolute;
		top: 50%;
		right: 14px;
		transform: translateY(-50%);
		font-size: 19px;
	}
}

.btn--phone {
	position: relative;
	padding-right: 46px;

	&:after {
		@include iconFont;
		@extend .icon--phone:before;
		position: absolute;
		top: 50%;
		right: 14px;
		transform: translateY(-50%);
		font-size: 26px;
	}
}

.btn--more {
	display: block;
	padding: 22px;
	background: $bgBtnMore;
	border: 0;
}

.btn--midi {
	padding: 20px 28px;
	font-size: 16px;
	letter-spacing: 1.83px;

	&:after {
		right: 30px;
	}
}

.btn--xl {
	width: 100%;
	max-width: 304px;
	padding: 20px 15px;
	font-size: 16px;
	letter-spacing: 1.83px;
	text-align: center;
}

.btn--wide {
	display: block;
	width: 100%;
	padding: 20px 15px;
	text-align: center;
}

.btn--t-center {
	text-align: center;	
}

.btn--submit {
	width: 100%;
	padding: 20px 15px;
	font-size: 16px;
	letter-spacing: 1.83px;
	text-align: center;
}

.btn--disabled {
	pointer-events: none;
	background: $disabledBg;
	border-color: $disabledBorder;
	color: $disabledClr;
}