/* ==========================================================================
    SECTION
   ========================================================================== */
.section {
	@include clearfix;
	box-sizing: border-box;
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 20px;
}

.section--midi {
	max-width: 1140px;
	padding: 0;
}


.section--xl {
	max-width: 1440px;
	padding: 0;
}