/* ==========================================================================
    HEADER
   ========================================================================== */
.header {
	box-sizing: border-box;
	position: fixed;
	z-index: 8;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 70px;
	padding: 0 20px;

	@include media-tablet {
		height: 96px;
		padding: 0 40px;
	}
}

.header__bg {
	transition: opacity 0.3s ease-out 0s;
	position: absolute;
	z-index: 0;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255,255,255, 0.7);
	backdrop-filter: blur(5px);
	box-shadow: 0 0 10px 0 rgba(0,0,0, 0.3);
	opacity: 0;

}

.document-scrolled .header__bg {
	opacity: 1;
}

.header__main {
	transition: transform 0.3s ease-out 0s;
	position: fixed;
	z-index: 9;
	top: 0;
	left: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
	height: 100%;
	background-image: linear-gradient(181deg, #fff1e7 0%, #fff1e7 64%, #fff1e7 100%);

	@include media-tablet {
		width: 360px;
	}

	@include media-desktop {
		position: relative;
		left: inherit;
		justify-content: flex-end;
		width: inherit;
		height: auto;
		background: transparent;
	}
	
}

.nav-open .header__main {
	transform: translateX(-100%);

	@include media-desktop {
		transform: none;
	}
}

.header__sub {
	position: absolute;
	bottom: 20px;
	left: 0;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	width: 100%;

	@include media-desktop {
		position: relative;
		bottom: auto;
		left: auto;
		width: auto;
	}
}

.header__btns {
	padding: 0 0 0 12px;
}