/* ==========================================================================
    TYPOGRAPHY
   ========================================================================== */
p {
	margin: 0; 
	padding: 13px 0;
}

strong {
	font-weight: 700;
} 

a {
	@include trans1;
	color: $clrLinks;
	text-decoration: none;

	&:hover {
		color: $clrLinksActive;
		text-decoration: none;
	}

	&:focus {
		color: $clrLinksActive;
		text-decoration: none;
	}

	&:active {
		color: $clrLinksActive;
		text-decoration: none;
	}

}

a[href^="tel"],
a[href^="sms"] {
	color: $clrTxt;
	text-decoration: none;
	cursor: default;
}

h1 {
	@include subFont;
	margin: 0;
	padding: 13px 0 30px 0;
	line-height: 1.2;
	font-size: 36px;
	font-weight: 700;

	@include media-tablet {
		padding-bottom: 70px;
		font-size: 48px;
	}

	&:after {
		content: '';
		display: block;
		width: 80px;
		height: 2px;
		margin: 16px 0 0 0;
		background: $clrBrown;

		@include media-tablet {
			margin-top: 29px;
		}
	}
}

h1.headline {
	letter-spacing: 3px;
	text-transform: uppercase;
}

h1.headline--center {
	text-align: center;

	&:after {
		margin-left: auto;
		margin-right: auto;
	}
}

h2 {
	@include subFont;
	margin: 0;
	padding: 13px 0 30px 0;
	line-height: 1.2;
	font-size: 32px;
	font-weight: 700;
	letter-spacing: 2.2px;

	&:after {
		content: '';
		display: block;
		width: 80px;
		height: 2px;
		margin: 16px 0 0 0;
		background: $clrBrown;

		@include media-tablet {
			margin-top: 25px;
		}
	}
}

h2.headline {
	text-transform: uppercase;
}

h2.headline--xl {
	font-size: 36px;

	@include media-tablet {
		font-size: 48px;
	}
}

h2.headline--center {
	text-align: center;

	&:after {
		margin-left: auto;
		margin-right: auto;
	}
}

h3 {
	@include subFont;
	margin: 0;
	padding: 13px 0 15px 0;
	line-height: 1.2;
	font-size: 28px;
	font-weight: 700;
}

h4,
h5, 
h6 {
	@include subFont;
	margin: 0;
	padding: 13px 0 15px 0;
	line-height: 1.2;
	font-size: 24px;
	font-weight: 700;
}

ul {
	margin: 0;
	padding: 10px 0;
}

ul li {
	position: relative;
	margin: 0;
	padding: 4px 0 4px 20px;
	line-height: 1.6;
	list-style: none;
}

ul li:before {
	content: '';
	position: absolute;
	top: 13px;
	left: 0;
	width: 6px;
	height: 6px;
	background: $clrPrimary;
	border-radius: 3px;

	@include media-tablet {
		top: 15px;
	}
}

ul ul {
	padding: 7px 0 0 0;
}

ol {
	margin: 0;
	padding: 7px 0;
	counter-reset: section;
}

ol ul {
	counter-reset: section; 
}

ol li {
	position: relative;
	margin: 0;
	padding: 4px 0 4px 24px;
	line-height: 1.4;
	list-style: none;
}

ol li:before {
	@include subFont;
	counter-increment: section;
	content: counters(section,".") ".";
   	position: absolute;
	top: 5px;
	left: 0;
	color: $clrPrimary;

}

ol ol li {
	padding-left: 34px;
}

ol ul li {
	padding-left: 20px;
}

ol ul li:before {
	content: '';
	top: 13px;
	left: 0;

	@include media-tablet {
		top: 15px;
	}
}

ol ol {
	padding: 7px 0 0 0;
}

dl {
	margin: 0;
	padding: 11px 0 6px 0;
	line-height: 1.4;
}

dt {
	@include subFont;
	margin: 0;
	padding: 0 0 6px 0;
	font-weight: 700;
}

dd {
	margin: 0;
	padding: 0 0 6px 0;
}

table {
	margin: 13px 0;
	text-align: left;
	border: 1px solid $clrPrimary;
	line-height: 1.6;
}

table th,
table thead td {
	padding: 5px 10px;
	background: $bgTable;
	border: 1px solid $clrPrimary;
	font-weight: 700;
	text-align: left;
	vertical-align: middle;
}


table td {
	padding: 5px 10px;
	border: 1px solid $clrPrimary;
	vertical-align: middle;
}

table p {
	padding: 0;
}

table.width-100 {
	width: 100%;
}

table.no-border {
	border: 0;
	border-top: 1px solid $clrPrimary;
}

table.no-border th,
table.no-border td {
	border: 0;
	border-bottom: 1px solid $clrPrimary;
}

cite {
	box-sizing: border-box;
	float: left;
	width: 100%;
	clear: both;
	margin: 13px 0;
	padding: 10px 0 10px 20px;
	border-left: 2px solid $clrPrimary;
}

blockquote {
	position: relative;
	margin: 13px 0;
	padding: 46px 16px 5px 16px;
	background: $clrPrimary;
}

blockquote:after {
	content: 'â€œ';
	position: absolute;
	top: 24px;
	left: 50%;
	width: 28px;
	height: 19px;
	margin-left: -14px;
	color: #fff;
	font-family: Georgia, serif;
	font-size: 56px;
	line-height: 38px;
	font-weight: bold;
	text-align: center;
}

hr {
	height: 1px;
	margin: 13px 0;
	padding: 0;
	background: $clrBrown;
	border: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
	margin: auto;
}

img.left {
	display: block;
	margin: 20px 40px 10px 0;

	@include media-phone {
        float: none;
		margin: 20px auto 10px auto;
    }
}

img.right {
	display: block;
	margin: 20px 0 10px 40px;

	@include media-phone {
        float: none;
		margin: 20px auto 10px auto;
    }
}

.promo {
	@include subFont;
	margin-bottom: -2px;
	padding-bottom: 0;
	color: $clrBrown;
	font-size: 14px;
	letter-spacing: 1.6px;
	font-weight: 500;
	text-transform: uppercase;
}

.midi {
	font-size: 14px;

	@include media-tablet {
		font-size: 16px;
	}
}

.pink {
	color: $clrPrimary;
}